.vignette {
  box-shadow: 0 0 100px rgba(0, 0, 0, 0.1) inset;
  height: 100vh;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: -100;
  &.fixed {
    position: fixed;
  }
  @media (min-width: 1024px) {
    box-shadow: 0 0 200px 80px rgba(0, 0, 0, 0.15) inset;
  }
}
